<script>

export default {
  name: 'noticeList',
  props: {
  },
  data: () => {
    return {
    }
  },
  mounted () {
    const t = this;
  },
  created () {
  },
  methods: {
    chkAuthorCde() {
      let isAllChk = true;
      const bookList = document.querySelectorAll('[name="bookWithoutAuthorCde"]');
      const allChk = document.querySelector('[name="booksWithoutAuthorCde"]');
      bookList.forEach((el) => {
        if(!el.checked){
          isAllChk = false;
          return false;
        }
      });

      allChk.checked = isAllChk;
    },

    chkAllAuthorCde() {
      const allChk = document.querySelector('[name="booksWithoutAuthorCde"]');
      const bookList = document.querySelectorAll('[name="bookWithoutAuthorCde"]');
      bookList.forEach((el) => {el.checked = allChk.checked;});
    },

    getBookInfoWithPopup() {
      const T = this;
      const ISBN = T.$refs.isbn.value;
  
      T.$axios.post(`${T.$store.state.serverUrl}/getBookInfoByIsbn`, {isbn: ISBN}).then((res) => {
        const { data } = res;
        const parser = new DOMParser();
        const xmlDoc = parser.parseFromString(data, "text/xml");
        const books = T.$xmlToObjArr({xml: xmlDoc, tagName: 'RECORD'});

        console.log(books);

        T.$store.state.booksByIsbn = books;
        T.$store.state.ref.libMng.popup.view = 'regBookPop';
      });
      
    }
  }
}
</script>

